
.checkout-page {
  padding: 16px;

  &__title {
    font-weight: 800;
    font-size: 24px;
    line-height: 120%;
    margin: 0 0 24px;
  }

  &__subtitle {
    font-weight: 800;
    font-size: 20px;
    line-height: 120%;
    margin: 0 0 16px;
  }

  &__footer {
    background: #fff;
    box-shadow: 0px -4px 15px rgba(22, 25, 31, 0.1);
    padding: 16px;
    position: fixed;
    z-index: 99;
    bottom: 0;
    left: 0;
    right: 0;
  }
}

.form {
  &__group {
    margin-bottom: 16px;
  }

  &__label {
    font-weight: 700;
    display: inline-block;
    margin-bottom: 8px;
  }

  &__field {
    display: block;
    width: 100%;
    background: #FFFFFF;
    border: 1px solid #E5EAEF;
    border-radius: 8px;
    padding: 12px 16px;

    &:focus {
      outline: none;
    }

    &::-webkit-input-placeholder {
      color: #8592AA;
    }

    &::-moz-placeholder {
      color: #8592AA;
      opacity: 1;
    }

    &:-moz-placeholder {
      color: #8592AA;
    }

    &:-ms-input-placeholder {
      color: #8592AA;
    }

    &::-ms-clear {
      display: none;
    }

    &::-webkit-search-cancel-button {
      display: none;
    }

    &.error {
      border-color: #FF1D1D;
    }
  }

  textarea {
    resize: none;
  }
}

.total-block {
  margin-bottom: 16px;

  &__row{
    font-weight: 600;
    font-size: 16px;
    line-height: 120%;
    margin: 0 0 5px;
  }

  &__value {
    font-weight: 800;
    font-size: 18px;
    line-height: 120%;
  }

  &__currency {
    font-weight: 800;
    font-size: 14px;
    line-height: 120%;
  }

  &__quantity {
    font-weight: 600;
    color: #8592AA;
    margin: 0;
  }
}

.error-message {
  display: block;
  text-align: center;
}

.test {
  position: fixed;
  z-index: 1000;
  right: 0;
  top: 0;
  bottom: 0;
  overflow: auto;
  border: 3px solid #000;
  padding: 10px;
  background: #fff;
  font-size: 11px;
}

